.buttonsContainer {
    margin: 20px 0;
}
    .buttonsContainer button {
        height: 35px;
        width: 100px;
        border-radius: 7px;
        margin-right: 20px;
        border: none;
        background: #0066CC;
        color: #FFFFFF;
        font-size: 17px;
    }

.buttonsContainer button:disabled {
    opacity: 0.4;
}

.clock {
    margin: 20px 0;
    font-size: 20px;
}

.watch {
    margin: 10px 0;
}

.watch strong {
    margin-top: 10px;
}

.more {
    color: rgba(0, 0, 0, 0.5);
    font-size: 17px;
}