.header {
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    box-shadow:  0px 5px 5px rgba(0, 0, 0, 0.48);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 65px;
    width: 100%;
}

.header h1 {
    margin: 10px 50px;
    font-size: 25px;
    font-weight: 600;
}